.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
   }
   
   .toggle-button:focus {
    outline: none;
   }
   
   .toggle-button_line {
    width: 30px;
    height: 2px;
    background: white;
   }
.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #760EE8;
    height: 56px;
   }
   
   .toolbar_navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
   }
   
   .toolbar_logo {
    margin-left: 1rem;
   }
   
   .toolbar_logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
   }
   
   .spacer {
    flex: 1 1;
   }
   
   .toolbar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
   }
   
   .toolbar_navigation-items li {
    padding: 0 0.5rem;
   }
   
   .toolbar_navigation-items a {
    color: white;
    text-decoration: none;
   }
   
   .toolbar_navigation-items a:hover,
   .toolbar_navigation-items a:active{
    color: black;
   }
.side-drawer {
    height: 100%;
    background: white;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 50%;
    max-width: 300px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
   }
   
   .side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
   }
   
   .side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: top;
   }
   
   .side-drawer li {
    margin: 0.5rem 0;
   }

   .side-drawer a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem;
   }
   
   .side-drawer a:hover,
   .side-drawer a:active {
    color: #760EE8;
   }
   

   .body {
    margin: 0;
    margin-right: 20%;
    padding: 12%;
}

.search-box {
    position: absolute;
    top: 6%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: black;
    height: 40px;
    border-radius: 40px;
    padding: 10px;
}

.search-box:hover > .search-txt{
    width: 240px;
    padding: 0 6px;
    
}

.search-box:hover > i {
    background: white;
    display: none;
}


.search-btn {
    float: right;
    /* width: 20px;
    height: 20px; */
    border-right-style: 50%;
    /* background: white; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-txt {
    border: none;
    background: none;
    outline: none;
    float: left;
    padding: 0;
    color: white;
    font-size: 16px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    line-height: 20px;
    width: 0px;
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 100;
   }
.stopwatch {
  box-sizing: border-box;
  background: lightgray;
  padding: 20px;
  margin-left: 6%;
  margin-right: 75%;
  margin-top: 6%;
  margin-bottom: 1%;
  text-align: center;

}

.stopwatch-header {
  text-align: center;
  color: darkorchid;
  font-size: 20px;
  font: status-bar;
}

.stopwatch-display {
  text-align: center;
  font-size: 18px;
}


